import { StatusInject } from "./StatusDecorator";
import { AttrInject } from "./AttrDecorator";
import { BehaviorAttribute } from "../base/BehaviorBase";
import { GetTypeByValue, GetType } from "./TypeDecorator";
import { DecoratorBinder } from "../binder/DecoratorBinder";
import { Delay } from "../util/Delay";
import { DefineProperty } from "../util/Define";
export const CONST_behaviorBinder = new DecoratorBinder();
export function Behav(value) {
  return function (target, propertName) {
    CONST_behaviorBinder.bind(target.constructor).to(new DelayString(value)).toProperty(propertName);
  };
}
export function BehaviorInject(inst, target, isInject = true) {
  if (!inst) return;
  if (!target) target = inst;
  let behaviors = inst.value.behaviors;
  if (!behaviors) return;
  let decoratorClass = CONST_behaviorBinder.get(target.value.constructor);

  if (!behaviors.hasOwnProperty("__isInit__")) {
    Object.defineProperty(behaviors, "__isInit__", {
      get: function () {
        return true;
      }
    });

    for (let i = 0; i < behaviors.length; i++) {
      let type = GetTypeByValue(behaviors[i].value);
      let name = behaviors[i].value.class.value;

      if (name == null) {
        name = type;
        behaviors[i].value.class.value = type;
      }

      if (name == null || behaviors.hasOwnProperty(name)) continue;
      DefineProperty(behaviors, name, BehaviorAttribute, i);
    }
  }

  for (let binding of decoratorClass.list) {
    Object.defineProperty(target.value, binding.property, {
      get: function () {
        let prop = behaviors[binding.value.delay];

        if (prop == null) {
          DefineProperty(behaviors, binding.value.delay, BehaviorAttribute);
          return null;
        } else {
          return prop.value;
        }
      },
      set: function (value) {
        let prop = behaviors[binding.value.delay];

        if (prop == null) {
          DefineProperty(behaviors, binding.value.delay, BehaviorAttribute);
          prop = behaviors[binding.value];
        }

        prop.eq(value);
      }
    });
  }

  if (isInject) {
    let len = behaviors.length;

    for (let i = 0; i < len; i++) {
      let behavior = inst.value.behaviors[i];
      if (behavior == null) continue;
      behavior.value.root = inst;
      StatusInject(inst, behavior);
      AttrInject(inst, behavior);
      BehaviorInject(inst, behavior, false);

      if (behavior.value.isInit.value == null) {
        behavior.value.isInit.value = true;
        behavior.value.init();
      }

      behavior.value.enable();
    }
  }
}

class DelayString extends Delay {
  get() {
    if (typeof this.arg == "function") {
      return GetType(this.arg);
    } else {
      return this.arg;
    }
  }

}