import { GetConverter } from "./ConvertDecorator";
export const CONST_SettleMap = new Map();
export function Settle(cls) {
  return function (target) {
    CONST_SettleMap.set(cls, target);
  };
}
export function GetCtorOfSettler(cls) {
  return CONST_SettleMap.get(cls);
}
export function GetSettler(value, cls, index, parent) {
  return GetConverter(cls).settler(value, index, parent);
}