import { Writer, Reader } from "peng-converter";
import { GetConverter } from "./ConvertDecorator";
import { Utils } from "../util/Util";
import { Ctor, GetCtorByValue, GetCtor, GetSerial } from "./TypeDecorator";
import { GetSettler } from "./SettleDecorator";
import { DelayConverter, FieldBinder } from "../binder/FieldBinder";
import { NullException } from "../util/Exception";
export const CONST_FieldBinder = new FieldBinder();
export function GetFieldList(cls) {
  return CONST_FieldBinder.get(cls).list;
}
export function Field(type, identifyName, structure = null) {
  return function (target, propertName) {
    FieldFunc(target, type, propertName, identifyName, structure);
  };
}
export function FieldParseShell(value, cls, index, parent) {
  if (cls == null) {
    let type = value["@type"];
    cls = GetCtor(type);
  }

  let inst;

  if (!value) {
    inst = null;
  } else {
    inst = Ctor(cls);
  }

  let settler = GetSettler(inst, cls, index, parent);
  let list = GetFieldList(cls);

  for (let i = 0; i < list.length; i++) {
    let binding = list[i];
    let prop = value[binding.identity];
    let next;

    if (binding.structure.delay) {
      next = binding.structure.delay.parseShell(prop, binding.value.delay.cls);
    } else {
      next = binding.value.delay.parseShell(prop, binding.value.delay.cls);
    }

    next.parent = settler;
    next.index = i;
    inst[binding.property] = next;
  }

  return settler;
}
export function FieldParse(value, cls) {
  if (value == null) return null;

  if (cls == null) {
    let type = value["@type"];
    cls = GetCtor(type);
  }

  if (value == null) return null;
  let inst = Ctor(cls);
  if (!inst) return null;
  let list = GetFieldList(cls);

  for (let i = 0; i < list.length; i++) {
    let binding = list[i];
    let prop = value[binding.identity];
    let next;

    if (binding.structure.delay) {
      next = binding.structure.delay.parse(prop);
    } else {
      next = binding.value.delay.parse(prop);
    }

    inst[binding.property] = next;
  }

  return inst;
}
export function FieldJson(value, cls) {
  if (value == null) return null;

  if (cls == null) {
    cls = GetCtorByValue(value);
  }

  let inst = {};
  if (!inst) return null;
  let list = GetFieldList(cls);

  for (let i = 0; i < list.length; i++) {
    let binding = list[i];
    let prop = value[binding.identity];
    let next;

    if (binding.structure.delay) {
      next = binding.structure.delay.json(prop);
    } else {
      next = binding.value.delay.json(prop);
    }

    inst[binding.property] = next;
  }

  return JSON.stringify(inst);
}
export function FieldJsonShell(value, cls) {
  if (value == null || value.value == null) return null;

  if (cls == null) {
    cls = GetCtorByValue(value.value);
  }

  if (value == null) return null;
  let inst = {};
  if (!inst) return null;
  let list = GetFieldList(cls);

  for (let i = 0; i < list.length; i++) {
    let binding = list[i];
    let prop = value.value[binding.identity];
    let next;

    if (binding.structure.delay) {
      next = binding.structure.delay.jsonShell(prop);
    } else {
      next = binding.value.delay.jsonShell(prop);
    }

    inst[binding.property] = next;
  }

  return JSON.stringify(inst);
}
export function FieldToShell(writer, settle, cls) {
  if (settle == null || settle.value == null) throw NullException;

  if (cls == null) {
    cls = GetCtorByValue(settle.value);
  }

  let list = GetFieldList(cls);
  let validArr = Utils.GetValidArr(list, writer, binding => {
    let prop = settle.value[binding.property];

    if (prop != null) {
      if (binding.structure.delay != null) {
        return true;
      } else {
        if (prop.value != null) {
          return true;
        } else {
          return false;
        }
      }
    } else {
      return false;
    }
  });

  for (let i = 0; i < validArr.length; i++) {
    let binding = list[validArr[i]];
    let prop = settle.value[binding.property];

    if (binding.structure.delay) {
      binding.structure.delay.toShell(writer, prop, binding.value.delay.cls);
    } else {
      binding.value.delay.toShell(writer, prop);
    }
  }
}
export function FieldTo(writer, value, cls) {
  if (value == null) throw NullException;

  if (cls == null) {
    cls = GetCtorByValue(value);
  }

  let list = GetFieldList(cls);
  let validArr = Utils.GetValidArr(list, writer, binding => {
    if (value[binding.property] != null) {
      return true;
    } else {
      return false;
    }
  });

  for (let i = 0; i < validArr.length; i++) {
    let binding = list[validArr[i]];
    let prop = value[binding.property];

    if (binding.structure.delay) {
      binding.structure.delay.to(writer, prop);
    } else {
      binding.value.delay.to(writer, prop);
    }
  }
}
export function FieldFromShell(reader, cls, index, parent) {
  if (cls == null) {
    cls = GetCtor(reader.int32());
  }

  let settler = GetSettler(Ctor(cls), cls, index, parent);
  let list = GetFieldList(cls);
  Utils.ParseValidArr(reader, (isExist, i) => {
    let binding = list[i];
    let next;

    if (isExist) {
      if (binding.structure.delay) {
        next = binding.structure.delay.fromShell(reader, binding.value.delay.cls);
      } else {
        next = binding.value.delay.fromShell(reader);
      }
    } else {
      if (binding.structure.delay) {
        next = binding.structure.delay.settler(null);
      } else {
        next = binding.value.delay.settler(null);
      }
    }

    if (next != null) {
      next.parent = settler;
      next.index = i;
    }

    settler.value[binding.property] = next;
  });
  return settler;
}
export function FieldFrom(reader, cls) {
  if (cls == null) {
    let serial = reader.int32();
    cls = GetCtor(serial);
  }

  let inst = Ctor(cls);
  if (!inst) throw Error("[@Type]");
  let list = GetFieldList(cls);
  Utils.ParseValidArr(reader, (isExist, i) => {
    let binding = list[i];
    if (binding == null) debugger;
    let next;

    if (isExist) {
      if (binding.structure.delay) {
        next = binding.structure.delay.from(reader, binding.value.delay.cls);
      } else {
        next = binding.value.delay.from(reader);
      }
    } else {
      next = null;
    }

    inst[binding.property] = next;
  });
  return inst;
}
export function To(value, writer) {
  if (value == null) return null;
  if (writer == null) writer = new Writer();
  let cls = GetCtorByValue(value);
  writer.int32(GetSerial(cls));
  let converter = GetConverter(cls);

  if (converter) {
    converter.to(writer, value);
  } else {
    console.error("[]");
    return null;
  }

  return writer.finish();
}
export function ToShell(value, writer) {
  if (value == null) return null;
  if (writer == null) writer = new Writer();
  let cls = GetCtorByValue(value.value);
  writer.int32(GetSerial(cls));
  let converter = GetConverter(cls);

  if (converter) {
    converter.toShell(writer, value, cls);
  } else {
    console.error("[]");
    return null;
  }

  return writer.finish();
}
export function From(data, reader) {
  if (data == null || data.length == 0) return null;
  if (reader == null) reader = new Reader(data);
  let serial = reader.int32();
  let ctor = GetCtor(serial);
  let converter = GetConverter(ctor);

  if (converter != null) {
    return converter.from(reader, ctor);
  } else {
    console.error("[]");
    return null;
  }
}
export function FromShell(data, reader) {
  if (data == null || data.length == 0) return null;
  if (reader == null) reader = new Reader(data);
  let serial = reader.int32();
  let ctor = GetCtor(serial);
  let converter = GetConverter(ctor);

  if (converter != null) {
    return converter.fromShell(reader, ctor);
  } else {
    console.error("[]");
    return null;
  }
}
export function Parse(value) {
  let type = value["@type"];
  let ctor = GetCtor(type);
  let converter = GetConverter(ctor);

  if (converter != null) {
    return converter.parse(value, ctor);
  } else {
    console.error("[]");
    return null;
  }
}
export function ParseShell(value) {
  let type = value["@type"];
  let ctor = GetCtor(type);
  let converter = GetConverter(ctor);

  if (converter != null) {
    return converter.parseShell(value, ctor);
  } else {
    console.error("[]");
    return null;
  }
}
export function Json(value) {
  if (value == null) return null;
  let cls = GetCtorByValue(value);
  let converter = GetConverter(cls);

  if (converter) {
    return converter.json(value);
  } else {
    console.error("[]");
    return null;
  }
}
export function JsonShell(value) {
  if (value == null || value.value == null) return null;
  let cls = GetCtorByValue(value.value);
  let converter = GetConverter(cls);

  if (converter) {
    return converter.jsonShell(value);
  } else {
    console.error("[]");
    return null;
  }
}

function FieldFunc(target, cls, propertName, identifyName, structure) {
  CONST_FieldBinder.bind(target.constructor).to(new DelayConverter(cls)).toStructure(new DelayConverter(structure)).toProperty(propertName).toIdentify(identifyName);
}