import { Long } from "peng-converter";
const CONST_TypeMap = new Map();
export const CONST_CtorList = [];
export function Type(type) {
  return function (target) {
    TypeFunc(type, target);
  };
}

function TypeFunc(type, target) {
  target.type = type;
  CONST_TypeMap.set(type, target);
  target.serial = CONST_CtorList.length;
  CONST_CtorList.push(target);
}

export function Ctor(value) {
  if (value == null) return null;
  let ctor;

  if (typeof value == "string") {
    ctor = CONST_TypeMap.get(value);
  } else {
    ctor = value;
  }

  if (!ctor) return null;
  return new ctor();
}
export function GetCtor(value) {
  switch (typeof value) {
    case "number":
      return CONST_CtorList[value];

    case "string":
      return CONST_TypeMap.get(value);
  }
}
export function GetSerial(value) {
  switch (typeof value) {
    case "function":
      return value.serial;

    case "object":
      return GetSerial(value.constructor);

    case "string":
      return GetSerial(String);
  }
}
export function GetSerialByValue(value) {
  return GetSerial(GetCtorByValue(value));
}
export function GetType(value) {
  switch (typeof value) {
    case "function":
      let type = value.type;

      if (type == null) {
        type = Object.type;

        if (value != Object) {
          console.warn("[@Type]" + value.name + "[]" + type);
        }
      }

      return type;

    case "number":
      return GetType(GetCtor(value));
  }
}
export function GetCtorByValue(value) {
  let ctor;

  switch (typeof value) {
    case "number":
      ctor = Number;
      break;

    case "string":
      ctor = String;
      break;

    case "boolean":
      ctor = Boolean;
      break;

    case "object":
      ctor = value.constructor;
      break;

    default:
      console.error("[]");
  }

  return ctor;
}
export function GetTypeByValue(value) {
  return GetType(GetCtorByValue(value));
}

const InitFunc = function () {
  TypeFunc("Object", Object);
  TypeFunc("Uint8Array", Uint8Array);
  TypeFunc("Number", Number);
  TypeFunc("String", String);
  TypeFunc("Boolean", Boolean);
  TypeFunc("Long", Long);
  TypeFunc("Array", Array);
  TypeFunc("Map", Map);
}();