import { DecoratorBinder, DecoratorBinding } from "./DecoratorBinder";
import { Delay } from "../util/Delay";
import { GetConverter } from "../decorator/ConvertDecorator";
export class FieldBinder extends DecoratorBinder {
  bind(key) {
    return super.bind(key);
    ;
  }

  getRawBinding() {
    return new FieldBinding(this.resolver.bind(this));
  }

  get(target) {
    return super.get(target);
  }

  resolveBinding(binding, key) {
    super.resolveBinding(binding, key);
  }

}
export class FieldBinding extends DecoratorBinding {
  toStructure(structure) {
    this.structure = structure;
    return this;
  }

  toIdentify(identity) {
    this.identity = identity;
    if (this._resolver != null) this._resolver(this);
    return this;
  }

  toProperty(propert) {
    return super.toProperty(propert);
  }

  to(value) {
    return super.to(value);
  }

}
export class DelayConverter extends Delay {
  get() {
    if (this.arg == null) return null;
    let tmp = GetConverter(this.arg);

    if (tmp == null) {
      console.error("[]");
      tmp = GetConverter(Object);
    }

    return tmp;
  }

}
